<template>
  <div class="order-receivable">  
    <template>
      <el-row :gutter="20">
        <el-col :span="12">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>用户输入</span>
              <el-button style="float: right; padding: 3px 0" type="text" @click="reqRecommand()">生成推荐</el-button>
            </div>
            <div>
              <el-input  
                @keyup.enter.ctrl.native="reqRecommand()"            
                type="textarea"
                :rows="20"
                placeholder="请输入内容"
                v-model="input_text">
              </el-input>
            </div>
          </el-card>

          </br>
          
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>实体识别</span>
            </div>
            <el-row>
              <el-button
                size="small"
                type="primary"
                @click="openGraph(value,label[0])"
                v-for="(label,value) in entities">
                  {{value}}:{{label[0]}}
              </el-button>
            </el-row>
          </el-card>          
        </el-col>  


        <el-col :span="12" :offset="0">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>推荐内容</span>
            </div>

            <el-col> 
              <el-collapse v-model="activeNames" style="margin-top:-21px" v-if="recommand_text">  
                <template v-for="(i,index) in recommand_text"> 
                  <el-collapse-item 
                    :title="key"
                    :name="index"
                    v-for="(value,key,index) in i"
                    style="background:#545c64">
                    <div v-for="(j,index) in value" v-html="j"></div>                      
                  </el-collapse-item> 
                </template>
              </el-collapse>
            </el-col>
          </el-card> 
        </el-col>

      </el-row>
    </template>
  </div>
</template>

<script>
  import {getOrderReceivable, reqRecommand, entityGraph} from '../../axios/api' 
  
    export default {
        name: 'recommand',
        components: {          
        },
        data () {
            return {
              input_text: '',
              recommand_text: '',
              entities: {},
              activeNames: null,              
            }
        },
        mounted(){
        },
        methods: {
          openGraph(value,label){
            entityGraph(value,label).then(res=>{
              this.$store.state.graph_data = res.data
            })      
                
            this.$store.dispatch('dialogshowGraph')
          },
          
          reqRecommand(){
            var split_res = this.input_text.split("。");
            var last_st = split_res[split_res.length - 1];
            if(last_st.length == 0 && split_res.length > 1) {              
              last_st = split_res[split_res.length - 2];
            }
            reqRecommand(last_st).then(res=>{
              if(res.status === 200){
                if(res.data == 'not found'){
                  //this.recommand_text = [{"没有找到匹配的内容":[]}];
                  return;
                }
                this.activeNames = null;
                this.entities = res.data.entities;
                this.recommand_text = res.data.recommand;
              }
            })
          },                   
        }
    }
</script>
<style scoped>
  .el-row .el-date-editor,.el-input,.el-select{
    width: 100%;
    max-width: 300px;
  }
  .el-form-item{
  margin-bottom:0;
}
  .container .el-row{
    height:auto;
  }
  .el-row{
    margin-bottom: 0;
  }
  h5{
    margin: 0;
  }
  .el-table{
    margin-top: 8px;
  }
  .text {
    font-size: 14px;
  }

  .item {
    padding: 3px;
  }
</style>



